import React from 'react';
import Media from 'react-media';
import style from './seventhScreen.module.css';
import img from '../../../img/infoImg/technologic.png';
import imgW from '../../../img/infoImg/technologic.webp';
import imgMob from '../../../img/infoImg/technologicMob.png';
import imgMobW from '../../../img/infoImg/technologicMob.webp';

const SeventhScreen = () => (
  <div className={style.wrapper}>
    <h2 className={style.header}>Технологично и продуманно</h2>
    <div className={style.info}>
      <Media query={{ minWidth: 1200 }}>
        {matches =>
          matches ? (
            <picture>
              <source srcSet={imgW} type="image/webp" />
              <source srcSet={img} type="image/png" />
              <img className={style.img} src={img} alt="order" />
            </picture>
          ) : (
            <picture>
              <source srcSet={imgMobW} type="image/webp" />
              <source srcSet={imgMob} type="image/png" />
              <img className={style.img} src={imgMob} alt="order" />
            </picture>
          )
        }
      </Media>
      <ul className={style.text}>
        <li className={style.textItem}>Адаптивный дизайн</li>
        <li className={style.textItem}>Мобильная версия</li>
        <li className={style.textItem}>Защищенное соединение</li>
        <li className={style.textItem}>Поисковая оптимизация</li>
      </ul>
    </div>
  </div>
);

export default SeventhScreen;
